<template>
  <div id="use_box">
    <div class="weight p10">酒店管理系统使用</div>
    <div class="bottom_box p10">
      <div class="mr62">收费类型</div>
      <van-radio-group v-model="radio"
                       name='radio'
                       direction="horizontal">
        <van-radio name="1">收费</van-radio>
        <van-radio name="2">免费</van-radio>
      </van-radio-group>
    </div>
    <van-cell-group v-if="radio == '1'">
      <van-cell title="免费使用至"
                v-model="dataForm.endLineTime"
                is-link
                @click="showTime = true" />
      <van-field v-model="dataForm.roomPrice"
                 label="收费标准"
                 type="number"
                 placeholder="填写价格">
        <span slot="button"
              size="small"
              style="color:#888"
              type="text">元/间/夜</span>
      </van-field>
      <div style="display:flex;padding:10px 16px;">
        <div style="width:112px;">房间数量</div>
        <input type="text"
               v-model="dataForm.roomCap"
               placeholder="请输入软件使用房间数量">
      </div>
    </van-cell-group>
    <van-popup v-model="showTime"
               position="bottom"
               style="width:100%;">
      <van-datetime-picker type="date"
                           title="选择年月日"
                           @confirm='confirmTime'
                           @cancel='showTime = false'
                           :min-date="minDate"
                           :max-date="maxDate" />
    </van-popup>
    <van-button class="pass"
                @click="passApply">通过申请</van-button>
  </div>
</template>
<script>
import dayjs from 'dayjs';
import { RadioGroup, Radio, Field, Toast, Dialog } from 'vant';
import { PostBusinessAuditApprovalCommit } from '@/api/api_equip';
// import Vue from 'vue';
export default {
    data() {
        return {
            radio: '2',
            time: null,
            showTime: false,
            minDate: new Date(),
            maxDate: new Date(),
            dataForm: {
                auditApprovalId: this.$route.query.auditApprovalId,
                endLineTime: null,
                roomCap: null,
                roomPrice: null
            }
        };
    },
    watch: {
        radio(neww) {
            if (neww == '1') {
                this.dataForm.endLineTime = dayjs().format('YYYY-MM-DD');
            }
        }
    },
    created() {
        this.$store.commit('setHeader', '软件使用');
        this.dataForm.endLineTime = dayjs().format('YYYY-MM-DD');
        this.minDate = new Date(dayjs().format('YYYY-MM-DD'));
        this.maxDate = new Date(dayjs().add(1, 'year').format('YYYY-MM-DD'));
    },
    methods: {
        // 选择时间
        confirmTime(e) {
            this.dataForm.endLineTime = dayjs(e).format('YYYY-MM-DD');
            this.showTime = false;
        },
        // 通过申请
        passApply() {
            if (this.radio == '1' && !this.dataForm.roomPrice) {
                Toast('请填入价格');
                return false;
            }
            if (this.radio == '1' && !this.dataForm.roomCap) {
                Toast('请填入房间数量');
                return false;
            }
            if (this.radio == '2') {
                delete this.dataForm.endLineTime;
                delete this.dataForm.roomCap;
                delete this.dataForm.roomPrice;
            }
            Dialog.confirm({
                title: '通过申请?',
                message: `${this.$route.query.hotelName}`
            })
                .then(() => {
                    PostBusinessAuditApprovalCommit(this.dataForm).done((res) => {
                        Toast.success(res.msg);
                        setTimeout(() => {
                            this.$router.go(-2);
                        }, 900);
                    });
                })
                .catch(() => {
                    console.log('不通过');
                });
        }
    }
};
</script>
<style scoped>
#use_box {
    text-align: left;
    font-size: 0.8rem;
    line-height: 30px;
    color: #000;
}
.weight {
    font-weight: bold;
}
.bottom_box {
    background: #fff;
    display: flex;
}
.pass {
    width: 100%;
    background: #ff7a41;
    color: #fff;
    position: fixed;
    bottom: 0;
}
.mr30 {
    margin-right: 30px;
}
.p10 {
    padding: 10px 16px;
}
.mr62 {
    width: 95px;
    margin-right: 18px;
}
input::-webkit-input-placeholder {
    color: #c8c9cc;
}
::v-deep .van-field__label {
    width: 95px;
    margin-right: 18px;
}
::v-deep .van-cell__right-icon {
    min-width: 8rem;
    text-align: left;
    color: #000;
}
::v-deep .van-cell {
    font-size: 0.8rem;
    color: #000;
}
::v-deep .van-field__label {
    color: #000;
}
::v-deep .van-cell__value {
    color: #000;
}
::v-deep .van-field__control {
    width: 30%;
    color: #000;
}
::v-deep .van-radio__icon--checked .van-icon {
    border-color: #ff7a41;
    background: #ff7a41;
}
</style>



